@use "variables" as v;
//el nombre hace referencia a el tipo de dispositivo en el que se usara la aplicacion ejemplo mismo telefono, laptops pcs etc
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid($columnas, $separacion) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $separacion;
    @content;
}
@mixin resetearLista {
    margin: 0;
    padding: 0;
    list-style: none;
    @content;
}
