@use "base/variables" as v;
@use "base/mixins" as m;

.boletos {
	padding: 5rem 0;
	h3 {
		text-align: center;
		color: v.$blanco;
		margin: 2rem;
	}
	.contenedor-boletos {
		@include m.tablet {
			@include m.grid(2, 5rem);
		}
	}
}
.pase {
	color: v.$negro;
	.nombre-pase{
		text-align: center;
		font-size: 2.8rem;
		color: v.$blanco;
	}
	&-header,
	&-body,
	&-footer {
		padding: 0.5rem;
		p {
			text-align: center;
			color: v.$blanco;
			margin: 0;
		}
	}
	&-header {
		p{
			text-transform: uppercase;
		}
		background-color: v.$rosa;
		font-size: 1.6rem;
	}
	&-body {
		background-color: v.$blanco;
		ul{
			@include m.resetearLista;
			li,
			:nth-child(odd){
				text-transform: capitalize;
				background-color: v.$blanco;
				padding: 1rem;
				border-bottom: #8d8d8d;
			}
		}
	}
	&-footer {
		background-color: v.$morado;
		font-weight: 700;
	}
}
