@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}
.galeria-imagenes{
    picture{
        cursor: pointer;
    }
    @include m.resetearLista;
    @include m.grid(2, 2rem);
    @include m.telefono {
        @include m.grid(3, 2rem);
    };
    @include m.tablet {
        @include m.grid(4, 2rem);
    };
    @include m.desktop {
        @include m.grid(6, 2rem);
    };
}
.overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: darken($color: rgba(0, 0, 0, 0.65), $amount: 50);
    picture{
        display: block;
        max-width: 80rem;
    }
    .btn-cerrar{
        padding: 0;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: v.$blanco;
        color: v.$negro;
        height: 4rem;
        width: 4rem;
        font-family: v.$fuente_principal;
        font-weight: 700;
        border-radius: 50%;
        text-align: center;
        font-size: 1.6rem;
        &:hover{
            cursor: pointer;
        }
        &:active{
            transform: scale(1.15);
        }
    }

}