@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay {
        background: rgb(165, 3, 255);
        background: linear-gradient(90deg, rgba(165, 3, 255, 0.3) 0%, rgba(255, 0, 110, 0.5) 100%);
        height: 100%;
        width: 100%;
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video {
        width: auto;
        @include m.desktop {
            width: 100%;
        }
        display: block;
    }
}
.contenido-video {
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    p {
        background-color: v.$rosa;
        display: inline-block;
        color: v.$blanco;
        font-weight: 700;
        padding: 1rem 2rem;
        margin: 0;
    }
}
