@use "base/variables" as v;
@use "base/mixins" as m;

.header {
    background-color: v.$verde;
    h1 {
        color: v.$blanco;
        text-align: center;
        @include m.telefono {
            margin: 0;
        }
    }
    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    @include m.tablet {
        &.fijo{
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            box-shadow: 0.1rem 0.2rem 0.3rem rgba(48, 48, 48, 0.75);
        }
    }
}
.navegacion-principal {
    a {
        color: v.$blanco;
        font-size: 2.4rem;
        display: block;
        text-align: center;
        margin-bottom: 1rem;

        &:hover {
            color: v.$amarillo;
        }
    }
    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
}
